import { HttpClient } from "@angular/common/http";
import { Injectable } from "@angular/core";
import { environment } from "environments/environment";

@Injectable({
  providedIn: "root",
})
export class TeacherService {
  constructor(private http: HttpClient) {}

  getAllTeachers<T>(eduCompId: any, page: number) {
    return this.http.get<T>(
      `${environment.apiURL}usermanagement/returnUsersByEduComp/${eduCompId}/${page}`
    );
  }

  addEditTeacher<T>(obj: T) {
    return this.http.post(environment.apiURL + "Admin/initTeacher", obj);
  }

  assignSubjectToTeacher<T>(body: T) {
    return this.http.post(environment.apiURL + "teacher-subject", body);
  }

  getTeacherByID(id: any) {
    return this.http.get(
      environment.apiURL + "Admin/getTeacherByTeacherUserID/" + id
    );
  }

  getStudentByID(id: any) {
    return this.http.get(
      environment.apiURL + "Admin/getStudentByStudentID/" + id
    );
  }
}
