import { HttpClient } from '@angular/common/http';
import { Injectable } from '@angular/core';
import { environment } from 'environments/environment';


@Injectable({
    providedIn: 'root'
  })
export class AnnouncementService {

constructor(private http: HttpClient) { 
}

addAnnouncement(data: any) {
  return this.http.post(environment.apiURL + "AJAX/AddAnnouncement", data);
}


getAnnounceById(id: any) {
  return this.http.get(
    `${environment.apiURL}Announcement/get-announce-by-Id/${id}`
  );
}


getAnnouncements<T>(teacherUserID:number)
{
  return this.http.get<T>(
  `${environment.apiURL}Announcement/GetAnnouncement/${teacherUserID}`
  );
}
getAllAnnouncements<T>(teacherUserID:number,pageId:number)
{
  return this.http.get<T>(
  `${environment.apiURL}Announcement/GetAllAnnouncement/${teacherUserID}/${pageId}`
  );
}



deleteAttach(id:number) {
  return this.http.get(
    `${environment.apiURL}Announcement/delete-Attach-By-Id/${id}`
  );
}


}
